<template>
  <x-header>
    途铃百科
  </x-header>
</template>
<script>
  import XHeader from 'vux/dist/components/x-header'
  import { showAlert, showLoading, hideLoading } from '../../vuex/actions'

  export default {
    vuex: {
      getters: {},
      actions: {
        showAlert,
        showLoading,
        hideLoading
      }
    },
    data () {
      return {
        wikis: []
      }
    },
    route: {
      data (transition) {
        this.showLoading()
        this.$http.get('http://www.touring.com.cn/interface/wiki_on_mobiles/').then(function (response) {
          this.$set('wikis', response.data)
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      }
    },
    components: {
      XHeader
    }
  }
</script>



